// 2. Card Skeleton
export function SkeletonCard() {
  return (
    <div className="flex flex-col space-y-2 animate-pulse">
      {/* Aspect Ratio 2:3 agar sama dengan poster anime */}
      <div className="aspect-[2/3] w-full bg-muted rounded-lg relative">
        <div className="absolute inset-0 flex justify-center items-center text-muted-foreground/20 text-xl">üé¨</div>
      </div>
      <div className="space-y-1.5">
        <div className="h-4 bg-muted rounded w-5/6" />
        <div className="h-3 bg-muted rounded w-1/2" />
      </div>
    </div>
  )
}

// 3. Grid Skeleton (Re-usable)
export function SkeletonGrid({ count = 10 }: { count?: number }) {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className={GRID_CLASS}>
        {Array(count).fill(0).map((_, i) => (
          <SkeletonCard key={i} />
        ))}
      </div>
    </div>
  )
}

// ========================
// üì∫ Sections
// ========================

async function HeroSection() {
  // FIX: Inisialisasi tipe array secara eksplisit untuk menghindari error TypeScript
  let animes: any[] = [];

  try {
    const data = await getTopAnime(1);
    animes = data?.data ? data.data.slice(0, 5) : [];
  } catch (error) {
    console.error("Failed to fetch Hero data", error);
  }

  const { HeroCarousel } = await import('@/components/hero-carousel');

  return (
    <div className="mt-8 mb-8 sm:mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <HeroCarousel
        title="Featured Anime"
        animes={animes}
      />
    </div>
  );
}

async function TopSection() {
  const data = await getTopAnime(1)
  const animes = data?.data ? data.data.slice(0, 5) : []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-lg sm:text-2xl font-bold text-foreground">Top Anime</h2>
      </div>
      <div className={GRID_CLASS}>
        {animes.map((anime: any, index: number) => (
          <AnimeCard key={`${anime.mal_id}-${index}`} anime={anime} />
        ))}
      </div>
    </div>
  )
}

async function PopularSection() {
  const data = await getPopularAnime(1)
  const animes = data?.data || []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-lg sm:text-2xl font-bold text-foreground mb-4">Popular Anime</h2>
      <div className={GRID_CLASS}>
        {animes.map((anime: any, index: number) => (
          <AnimeCard key={`${anime.mal_id}-${index}`} anime={anime} />
        ))}
      </div>
    </div>
  )
}

async function TrendingSection() {
  const data = await getTopAnime(1)
  const animes = data?.data ? data.data.slice(5, 25) : []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-lg sm:text-2xl font-bold text-foreground mb-4">Trending This Week</h2>
      <div className={GRID_CLASS}>
        {animes.map((anime: any, index: number) => (
          <AnimeCard key={`${anime.mal_id}-${index}`} anime={anime} />
        ))}
      </div>
    </div>
  )
}

